import React, {Component} from 'react';
import { CSSTransitionGroup } from 'react-transition-group';
import { Progress } from 'reactstrap';
import '../css/skill.css';
const borderColor={
	borderColor:'#0FB2D4'
};
class Skill extends Component{
	render(){
		return(
			<CSSTransitionGroup
		      transitionName="worksTransition"
		      transitionAppear={true}
		      transitionAppearTimeout={500}
		      transitionEnter={false}
		      transitionLeave={false}>
			<section id='skill' className='pt-5 pb-5'> 
				<div className='container'>
					<div className='row text-center'>
						<div className='col-12'>
							<h4 className='mb-3'>Skills</h4>
							<hr style={borderColor}/>
							<div className='form-group ml-auto mr-auto'>
								HTML <Progress color="success" value='60'>60%</Progress>
							</div>
							<div className='form-group ml-auto mr-auto'>
								CSS <Progress color="info" value='70'>70%</Progress>
							</div>
							<div className='form-group ml-auto mr-auto'>
								JAVASCRIPT <Progress color="warning" value='50'>50%</Progress>
							</div>
							<div className='form-group ml-auto mr-auto'>
								Bootstarp <Progress color="info" value='70'>70%</Progress>
							</div>
							<div className='form-group ml-auto mr-auto'>
								SASS <Progress color="info" value='80'>80%</Progress>
							</div>
							<div className='form-group ml-auto mr-auto'>
								React <Progress color="success" value='50'>50%</Progress>
							</div>
							<div className='form-group ml-auto mr-auto'>
								Gulp <Progress color="warning" value='60'>60%</Progress>
							</div>
						</div>
					</div>
				</div>
			</section>
			</CSSTransitionGroup>
		);
	}
}
export default Skill;