import React,{Component} from 'react';
import {FaFacebook,FaTwitter,FaLinkedin,FaInstagram} from 'react-icons/fa';
import { CSSTransitionGroup } from 'react-transition-group';
import '../css/home.css';
import pic from'../images/sonu.jpg';
import Menu from '../components/Menu';
const social=[
	{socialContent:<FaFacebook />,link:'https://www.facebook.com/Sonunikki'},
	{socialContent:<FaInstagram/>,link:'https://www.instagram.com/mit_keshri/'},
	{socialContent:<FaTwitter/>,link:'https://twitter.com/Mitkeshri'},
	{socialContent:<FaLinkedin/>,link:'https://www.linkedin.com/in/amit-keshri-8320b0114/'}
];
class Home extends Component{
	constructor(){
		super();
		this.state={
			name:'Amit Keshri',
			desigation:'UI Developer'
		};
	}
	render(){
		return(
			<CSSTransitionGroup
		      transitionName="homeTransition"
		      transitionAppear={true}
		      transitionAppearTimeout={500}
		      transitionEnter={false}
		      transitionLeave={false}>
			<section id='home' className='pt-4'>
				<div className='container text-center'>
					<div className='row'>
						<div className='col-12'>
							<Menu />
							<img src={pic} alt='amit' className='img-fluid rounded-circle ml-auto mr-auto mt-5 d-block'/>
							<h1 className='text-white mt-4'>{this.state.name}</h1>
							<h2 className='text-white mt-3'>{this.state.desigation}</h2>
							<hr />
							<ul className='social'>
								{	
								social.map((item,index)=>
									<li key={index.socialContent} className='d-inline-block mr-3'>
										<a href={item.link} className='fa'>{item.socialContent}</a>
									</li>)
								}
							</ul>
						</div>
					</div>
				</div>
			</section>
			</CSSTransitionGroup>
		);
	}
}
export default Home;