import React, {Component} from 'react';
import {FaEnvelope,FaPhone,FaMap} from 'react-icons/fa';
import {Button,Form,FormGroup,Label,Input,FormText} from 'reactstrap';
import { CSSTransitionGroup } from 'react-transition-group';
import '../css/contact.css';
const info=[
	{icon:<FaEnvelope />,heading:'QUERIES',para:'info@amitkeshri.com'},
	{icon:<FaPhone />,heading:'CALL ME',para:'8468871783'},
	{icon:<FaMap/>,heading:'ADDRESS',para:'New Delhi'}
];
class Contact extends Component{
	render(){
		return(
			<CSSTransitionGroup
		      transitionName="aboutTransition"
		      transitionAppear={true}
		      transitionAppearTimeout={500}
		      transitionEnter={false}
		      transitionLeave={false}>
			<section id='Contact' className='pt-5 pb-5'> 
				<div className='container text-white text-center'>
					<div className='row'>
						<div className='col-12'>
							<h4 className='mb-3'>Contact Me</h4>
							<hr />
						</div>
					</div>
					<div className='row'>
					{
						info.map((item,index)=>
							<div className='col-md-4 col-sm-4 col-12 info' key={index.icon}>
								{item.icon}
								<h6>{item.heading}</h6>
								<p>{item.para}</p>
							</div>
						)
					}
					</div>
					<div className='row text-left'>
						<div className='col-12'>
							<Form className='w-50 ml-auto mr-auto' method="post" action='contact-page.php'>
								<FormGroup>
									<Label for='name'>Your Name:</Label>
									<Input type="text" name="name" id='name' placeholder='Enter Your Name' className='form-control' autoComplete='off'/>
								</FormGroup>
								<FormGroup>
									<Label for='email'>Your Email:</Label>
									<Input type="email" name="email" id='email' placeholder='Enter Your Email' className='form-control' autoComplete='off'/>
								</FormGroup>
								<FormGroup>
									<Label for='msg'>Your Message:</Label>
									<Input type='textarea' name="message" id='msg' placeholder='Enter Message' className='form-control' cols='3' rows='4'/>
								</FormGroup>
								<Button id='submit' type='submit' name='submit' color='primary' className='ml-auto mr-auto d-block pl-5 pr-5'>Submit</Button>
							</Form>
						</div>
					</div>
				</div>
			</section>
			</CSSTransitionGroup>
		);
	}
}
export default Contact;
Input.PropTypes={
	autoComplete:'off'
};