import React, {Component} from 'react';
import { CSSTransitionGroup } from 'react-transition-group';
import '../css/project.css';
import web1 from '../images/web1.jpg';
import web2 from '../images/web2.jpg';
import web3 from '../images/web3.jpg';
import web4 from '../images/web4.jpg';
import web5 from '../images/web5.jpg';
import web6 from '../images/web6.jpg';
const projects=[
	{image:web1,link:'https://www.trustedessayreviews.com',websiteName:'trustedessayreviews.com',websiteContent:'Tired of being befuddled by tall talks of assignment writing websites...'},
	{image:web2,link:'https://hireanerd.net/',websiteName:'hireanerd.net',websiteContent:'Do the endles task put a lot of strain on you...'},
	{image:web3,link:'https://essayassignmenthelp.com.au/',websiteName:'essayassignmenthelp.com.au',websiteContent:'Get the best essay writing service & essay help...'},
	{image:web4,link:'https://essaygator.com/',websiteName:'essaygator.com',websiteContent:'We began our journey a decade back with the aim of talking away all the academic essays...'},
	{image:web5,link:'https://assignmenthelp.us/',websiteName:'assignmenthelp.us',websiteContent:'Assignmenthelp.us has an impeccable team of assignment writers offering brilliant writing...'},
	{image:web6,link:'https://www.impactpcsupport.com',websiteName:'impactpcsupport.com',websiteContent:'Impact PC Support is an independent provider of remote tech support services...'}
];
const borderColor={
	borderColor:'#0FB2D4'
};
class Project extends Component{
	render(){
		return(
			<CSSTransitionGroup
		      transitionName="worksTransition"
		      transitionAppear={true}
		      transitionAppearTimeout={500}
		      transitionEnter={false}
		      transitionLeave={false}>
			<section id='portfolio' className='pb-5'>
				<div className='container text-center'>
					<div className='row'>
						<div className='col-12'>
							<h4>Our Portfolio</h4>
							<hr style={borderColor}/>
						</div>
					</div>
					<div className='row'>
						{	
							projects.map((item,index)=>
							<div className='col-lg-4 col-md-6 col-sm-6 col-12 mb-3'>
								<figure key={index.websiteName}>
									<img src={item.image} alt='websites' className='img-fluid' />
									<figcaption>
										<a href={item.link} target='_blank'>
											<h5 className='pt-5'>{item.websiteName}</h5>
											<hr />
											<p>{item.websiteContent}</p>
										</a>
									</figcaption>
								</figure>
							</div>)
						}
					</div>
				</div>
			</section>
			</CSSTransitionGroup>
		);
	}
}
export default Project;