import React, { Component } from 'react';
import {Link} from 'react-router-dom';
const MENU_LIST = [
  {display:'Home',link:'home'},
  {display:'Portfolio',link:'/project'},
  {display:'About',link:'/about'},
  {display:'Experience',link:'/experience'},
  {display:'Skill',link:'/skill'},
  {display:'Contact',link:'/contact'}
];

class Menu extends Component {
  constructor(){
    super();
    this.state={
      open:false
    }
  }
  toggleButton = () =>{
    this.setState({
      open:!this.state.open
    });
  }
  render() {
    return (
      <React.Fragment>
        <div className={`toggle ${this.state.open ? 'open' : ''}`} onClick={this.toggleButton}><span></span></div>
        <ul className={`menu-list ${this.state.open ? 'open' : ''}`}>
          {
            MENU_LIST.map((item,index) => <li key={index.display} className='d-inline-block text-uppercase'><Link to={item.link} className='nav-link'>{item.display}</Link></li>)
          }
        </ul>
      </React.Fragment>
    );
  }
}

export default Menu;
