import React, {Component} from 'react';
import {FaUser,FaGraduationCap,FaHistory,FaFile} from 'react-icons/fa';
import { CSSTransitionGroup } from 'react-transition-group';
import '../css/about.css';
const education=[
	{college:'(10th) Adarsh High School',date:'2004-2005'},
	{college:'(10+2) C.A.V Inter College',date:'2005-2007'},
	{college:'(BCA) Sikkim Manipal University',date:'2007-2010'},
	{college:'(MCA) Lovely Professional University',date:'2011-2014'}
];
class About extends Component{
	render(){
		return(
			<CSSTransitionGroup
		      transitionName="aboutTransition"
		      transitionAppear={true}
		      transitionAppearTimeout={500}
		      transitionEnter={false}
		      transitionLeave={false}>
			<section id='about'>
				<div className='container'>
					<div className='row'>
						<div className='col-lg-4 col-12'>
							
						</div>
						<div className='col-lg-4 col-md-6 col-12'>
							<h5 className='mb-4'><FaUser className='mr-1'/> About Me</h5>
							<p className='text-justify text-white'>I am a passionate towards User Interface Developer, It has been more than 5 years dedicated to this field and providing best experience to clients. Grabing things and upcoming technologies very fast, one of my good habits.</p>
							<p className='text-justify text-white mt-2'>You have an idea, you need someone to bring it to life. As a developer I always try to bring   new things or enhanced way to meet the demand of current ara's users!. This is how, I moved on so far! With my enrich knowledge and creativity. I can take your thoughts and help you to  get this to next level in your life.</p>
						</div>
						<div className='col-lg-4 col-md-6 col-12'>
							<h5 className='mb-4'><FaGraduationCap className='mr-1'/> Education</h5>
							<ul>
								{
									education.map((item,index) =>
									<li key={index.college} className='d-block mb-4'>
										<h6 className='mb-2'>{item.college}</h6>
										<p className='text-white'><FaHistory className='mr-3'/>{item.date}</p>
									</li>)
								}
							</ul>
						</div>
					</div>
				</div>
			</section>
			</CSSTransitionGroup>
		);
	}
}
export default About;